export default {
	data() {
		return {
			cates: [],
			active: 0,
			secondDate: []
		}
	},
	onLoad() {
		this.getPicsCate()
	},
	methods: {
		async getPicsCate () {
			const res = await this.$myRequest ({
				url: '/api/getimgcategory'
			})
			this.cates = res.data.message
		},
		async leftClick (index,id) {
			this.active = index
			const res = await this.$myRequest({
				url: '/api/getimages/'+id
			})
			this.secondDate = res.data.message
		},
		preView (current) {
			const urls = this.secondDate.map(item=>{
				return item.img_url
			})
			uni.previewImage({
				current,
				urls
			})
		}
	}
}
